body {
  margin: 0;
  font-family: Arial, sans-serif;
}

.dashboard {
  display: flex;
  height: 100vh;
  overflow: hidden;
}

.sidebar-toggle {
  display: none;
  position: fixed;
  top: 20px;
  left: 20px;
  background: #2c3e50;
  color: white;
  border: none;
  padding: 10px;
  border-radius: 50%;
  cursor: pointer;
  z-index: 1000;
}

.side-nav {
  width: 250px;
  background: #2c3e50;
  color: white;
  padding: 20px;
  box-sizing: border-box;
}

.side-nav .logo {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 30px;
}

.side-nav .logo img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin-right: 10px;
}

.side-nav nav ul {
  list-style: none;
  padding: 0;
}

.side-nav nav ul li {
  display: flex;
  align-items: center;
  padding: 15px 0;
  cursor: pointer;
}

.side-nav nav ul li:hover {
  background: #34495e;
  border-radius: 5px;
}

.side-nav nav ul li svg {
  margin-right: 10px;
}

.main-content {
  flex: 1;
  box-sizing: border-box;
  overflow-y: auto;
}

.search-bar {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.search-bar input {
  width: 100%;
  padding: 10px;
  margin-left: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.dashboard-section,
.transaction-receipts,
.form-section {
  background: white;
  padding: 20px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  margin-bottom: 20px;
  border-radius: 5px;
}

.navigation-buttons {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

.navigation-buttons button {
  background: #2c3e50;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
}

.navigation-buttons button:hover {
  background: #34495e;
}

.form-indicator {
  text-align: center;
  margin-top: 10px;
}

.hidden {
  display: none;
}

@media (max-width: 768px) {
  .sidebar-toggle {
    display: block;
  }

  .side-nav {
    position: fixed;
    top: 0;
    left: -250px;
    height: 100%;
    transition: left 0.3s ease;
    z-index: 999;
  }

  .side-nav.open {
    left: 0;
  }

  .main-content {
    padding: 20px;
    flex: 1;
  }

  .main-content.search-bar.hidden {
    display: none;
  }
}
