.submission-details {
  font-family: Arial, sans-serif;
  padding: 20px;
}

h1 {
  text-align: center;
  margin-bottom: 20px;
}

.actions {
  text-align: center;
  margin-top: 20px;
}

button {
  margin: 5px;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  background-color: #007bff;
  color: white;
  font-size: 16px;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}

h2 {
  background-color: green;
  color: white;
  padding: 10px;
  margin: 10px 0;
  border-radius: 5px;
}

.details-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.personal-info {
  display: flex;
  flex-direction: row;
  gap: 20px;
}

.profile-image {
  flex: 1;
  min-width: 500px;
  height: 400px;
  background: #000;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.profile-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.user-details {
  display: flex;
  flex-wrap: wrap;
}

.user-details p {
  flex: 1 1 30%;
  min-width: 200px;
}

.education-details {
  width: 100%;
  border-collapse: collapse;
  margin: 20px 0;
}

.education-details th,
.education-details td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}

.education-details th {
  background-color: #f2f2f2;
}

.education-details tr:nth-child(even) {
  background-color: #f9f9f9;
}

.olevel-results-container {
  margin-bottom: 20px;
}

.sitting-heading {
  font-size: 1.5em;
  font-weight: bold;
  margin-bottom: 10px;
}

.sitting-heading.first {
  color: #007bff;
}

.sitting-heading.second {
  color: #28a745;
}

.olevel-results, .subjects-table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 20px;
}

.olevel-results td, .subjects-table td {
  padding: 8px;
  border: 1px solid #ddd;
}

.olevel-results th, .subjects-table th {
  padding: 8px;
  background-color: #f2f2f2;
  border: 1px solid #ddd;
}

.olevel-results thead, .subjects-table thead {
  background-color: #f2f2f2;
}

.sign {
  text-align: end;
}

@media print {
  body * {
    visibility: hidden;
  }
  
  .submission-details, 
  .submission-details * {
    visibility: visible;
  }

  .submission-details {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    font-size: 8pt;
  }

  .actions {
    display: none;
  }

  .details-container {
    page-break-inside: avoid;
  }

  .personal-info {
    display: flex;
    flex-direction: row;
    gap: 10px;
  }

  .profile-image {
    flex: 1;
    min-width: 100px;
    height: 100px;
  }

  .user-details p {
    font-size: 7pt;
  }

  h1, h2, h3 {
    page-break-after: avoid;
  }

  table {
    page-break-inside: avoid;
    font-size: 7pt;
  }
}

@media (max-width: 768px) {
  .details-container {
    padding: 10px;
  }

  .personal-info {
    flex-direction: column;
  }

  .profile-image, .user-details {
    flex: none;
    max-width: 100%;
  }

  .profile-image {
    flex: 1;
    min-width: 100px;
    height: 100px;
  }

  .profile-image img {
    width: 100%;
  }

  .education-details, .olevel-results, .subjects-table {
    font-size: 14px;
  }

  .button-container {
    flex-direction: column;
  }

  .print-button, .cancel-button {
    margin: 5px 0;
  }
}

@media (max-width: 480px) {
  .personal-info {
    padding: 5px;
  }

  .education-details, .olevel-results, .subjects-table {
    font-size: 12px;
  }
}
