.bio_form {
  max-width: 800px;
  margin: auto;
  background-color: white;
  border-radius: 8px;
  position: relative;
}

.form-group {
  display: flex;
  gap: 20px;
  margin-bottom: 20px;
}

.form-field {
  flex: 1;
  position: relative;
}

.form-field input,
.form-field select {
  width: 100%;
  padding: 12px 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  background-color: white;
  font-size: 16px;
}

.form-field label {
  position: absolute;
  top: 10px;
  left: 10px;
  font-weight: bold;
  color: #4CAF50;
  transition: all 0.2s ease;
}

.form-field input:not(:placeholder-shown) + label,
.form-field input:focus + label,
.form-field select:not(:placeholder-shown) + label,
.form-field select:focus + label {
  top: -20px;
  left: 10px;
  font-size: 12px;
  color: #4CAF50;
  background-color: white;
  padding: 0 5px;
}

.preview-container {
  margin-top: 10px;
  max-width: 500px;
}

.preview-container img {
  width: 100%;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.bio_form button {
  background-color: #4CAF50; 
  color: white;
  border: none;
  padding: 10px 15px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
}

.bio_form button:hover {
  background-color: #45a049;
}
