.edu_form {
  background-color: #ffffff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  max-width: 800px;
  box-sizing: border-box;
  margin: 0 auto;
}

.edu_input-group {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin-bottom: 20px;
}

.edu_input {
  position: relative;
  flex: 1;
  min-width: calc(50% - 20px);
}

.edu_input input {
  width: 100%;
  padding: 16px 8px;
  border: 1px solid #ddd;
  border-radius: 4px;
  box-sizing: border-box;
  font-size: 16px;
}

.edu_input label {
  position: absolute;
  left: 8px;
  top: 16px;
  font-size: 16px;
  color: #333;
  pointer-events: none;
  transition: 0.2s ease;
}

.edu_input input:focus + label,
.edu_input input:not(:placeholder-shown) + label {
  top: -8px;
  left: 8px;
  font-size: 12px;
  color: #4CAF50;
}

.edu_button {
  background-color: #4CAF50;
  border: none;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 4px;
}

.edu_button:hover {
  background-color: #45a049;
}

.education-details {
  margin-top: 20px;
  overflow-x: auto;
}

.education-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 10px;
}

.education-table th,
.education-table td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}

.education-table th {
  background-color: #f4f4f4;
}

.education-table tr:nth-child(even) {
  background-color: #f9f9f9;
}

@media (max-width: 768px) {
  .edu_input-group {
    flex-direction: column;
    gap: 10px;
  }

  .edu_input {
    min-width: 100%;
  }

  .edu_button {
    width: 100%;
    padding: 12px;
    font-size: 18px;
  }

  .education-details {
    margin-top: 15px;
  }

  .education-table {
    font-size: 14px; 
  }
}

@media (max-width: 480px) {
  .edu_form {
    padding: 15px;
  }

  .edu_button {
    padding: 10px;
    font-size: 16px;
  }

  .education-table th,
  .education-table td {
    padding: 6px;
  }

  .education-table th {
    font-size: 14px;
  }

  .education-table td {
    font-size: 13px;
  }
}
