.form-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.o_level_form {
  display: flex;
  justify-content: space-between;
  width: 100%;
  flex-wrap: wrap;
}

.sitting-group {
  width: 100%;
  border: 1px solid #ccc;
  padding: 1rem;
  border-radius: 8px;
  position: relative;
  margin-bottom: 1rem;
}

.input-fields-container {
  display: flex;
  flex-wrap: wrap;
  gap: 0.8rem;
  margin-bottom: 20px;
}

.input-fields {
  flex: 1 1 calc(50% - 0.5rem);
  box-sizing: border-box;
  margin-bottom: 1rem;
  position: relative;
}

.input-group {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.input-group .input-fields:nth-of-type(1) {
  margin-right: 1rem; /* Adds space between the subject and grade input fields */
}

.input-fields input,
.input-fields select {
  width: 100%;
  padding: 0.5rem;
  border: 1px solid #ccc;
  border-radius: 4px;
  outline: none;
  font-size: 1rem;
  background: none;
}

.input-fields input:focus,
.input-fields select:focus {
  border-color: #4CAF50;
}

.input-fields label {
  position: absolute;
  top: 0.75rem;
  left: 0.5rem;
  font-size: 1rem;
  color: #aaa;
  pointer-events: none;
  transition: all 0.2s;
}

.input-fields input:focus + label,
.input-fields input:not(:placeholder-shown) + label,
.input-fields select:focus + label,
.input-fields select:not(:placeholder-shown) + label {
  top: -0.75rem;
  left: 0.5rem;
  font-size: 0.75rem;
  color: #4CAF50;
  background: white;
  padding: 0 0.25rem;
}

.input-fields button {
  background-color: #ff4d4d;
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  cursor: pointer;
  margin-left: 0.5rem;
}

.add-subject-btn,
.submit-btn {
  background-color: #4CAF50;
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 1rem;
}

.remove-subject-btn {
  width: 20px;
  text-align: center;
  align-items: center;
  justify-content: center;
  background: red;
}

h3 {
  text-align: center;
}

@media (max-width: 768px) {
  .o_level_form {
    flex-direction: column;
    align-items: center;
  }

  .sitting-group {
    width: 100%;
    margin-bottom: 1rem;
  }

  .input-fields-container {
    flex-direction: column;
  }

  .input-fields {
    flex: 1 1 100%;
  }

  .input-group {
    flex-direction: row;
  }

  .input-group .input-fields:nth-of-type(1) {
    margin-right: 1rem;
  }

  .input-group .input-fields:nth-of-type(2) {
    margin-right: 0;
  }
}
